.grid-col {
  @include mq(medium) {
    @include g-columns;
	}
}


[class^='grid-col-'] {
  @include mq(small) {
    margin-bottom: em(12px);
    margin-top: em(12px);
	}

  @include mq(medium) {
    @include doubly(percentage($g-gutter-width / $context));
    float: left;
    min-height: 1px;
    padding-left: 10px;
    padding-right: 10px;

    &:last-of-type {
      float: right;
		}
	}
}
